import React,{Component}from 'react';
class Counter extends Component {
    state={
        count:0,
        tags:['item1','item2','item3','item4','item5']
    }
    handleIncreament=()=>{
        console.log("CLICKED",this,this.state.count)
        this.setState({count:this.state.count+1})
    
    }
    renderList(){
        if(this.state.tags.length===0)return <p>THERE IS NO ITEMS</p>
        return  <ul>{this.state.tags.map((tag,index)=><li key={index}>{tag}</li>)}</ul>
    }
    render() { 
        return (
             <React.Fragment>
            <h6 style={{color:"blue"}}>NAV</h6>
        <span className={this.reactToCount()}>{this.respondToCount()}</span>
            <button className="plus" onClick={this.handleIncreament}>+</button>
            <ul>
                {this.state.tags.length===0&&'ADD NEW ITEMS'}
                {this.renderList()}
            </ul>
        </React.Fragment>
        )
    }
    respondToCount(){
        const count=this.state.count
       return count===0?'Zero':count;
    }
    reactToCount(){
        return this.state.count===0?'zero':'no-zero'
    }
}
export default Counter;