import React,{Component} from 'react';
import {products} from '../index'
class Cart extends Component {
    state={products:products};
    handleOnPlusQuantity=id=>{
    let newProduct=this.state.products;
    newProduct[id].quantity=newProduct[id].quantity+1
    console.log(id);
    this.setState({products:newProduct});
    };
    handleOnMinusQuantity=id=>{
        let newProduct=this.state.products;
        console.log(id);
        if(newProduct[id].quantity!==0){
            newProduct[id].quantity=newProduct[id].quantity-1
        }
        this.setState({products:newProduct});
    }
    navLogic(){
        const product=this.state.products
        let totalCost=0;
        let totalItems=0;
        product.forEach(item=>{
            totalItems+=item.quantity
            totalCost+=(item.quantity*item.price)
        })
        return <h2>TOTAL COST IS :{totalCost.toFixed(2)}   TOTAL NUMBER OF ITEMS ARE:{totalItems}</h2>
    }
    makeTotal(item){
        let total=item.price * item.quantity
        return (
        <p>${total.toFixed(2)}</p>
        )
    }
    render() { 
        return (
        <div>
            <div className="nav">{this.navLogic()}</div>
            <table>
                <thead>
                    <tr>
                        <th>Product</th>
                        <th>Price</th>
                        <th>Quantity</th>
                        <th>Total</th>
                    </tr>
                </thead>
                <tbody>
                    {this.state.products.map((item,id)=>{
                        return(                       
                        <tr key={id}>
                            <td className="product-cell">
                            <span className="product">
                            <button className="cancel">X</button>
                            <img src={item.image} alt="item"/>
                            <p>{item.name}</p>
                        </span>
                            </td>
                            <td>
                                ${item.price}
                            </td>
                            <td>
                                <span className="quantity">
                                <button className="control" onClick={()=>this.handleOnPlusQuantity(id)}>
                                    +
                                </button>
                                <p>{item.quantity}</p>
                                <button className="control"  onClick={()=>this.handleOnMinusQuantity(id)}>
                                    -
                                </button>
                                </span>
                            </td>
                            <td>
                                {this.makeTotal(item)}
                            </td>
                        </tr>)
                    })}
                </tbody>
                <button className="buy">BUY NOW</button>
            </table>
            
            </div>
          );
    }
}
export default Cart;

